import { AuthenticationContext } from "@axa-fr/react-oidc-context";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { User } from "oidc-client";
import React from "react";
import { ItemQuery, ItemQueryOptions } from "../dataContracts/ItemQuery";
import { PricedItem } from "../dataContracts/PricedItem";
import ApiService from "../services/ApiService";
import { ItemField, ItemFieldSort } from "../utils/ItemField";

export type SortingBarProps = {
    name: string;
    onLoadingStart(): void;
    onLoadingStop(): void;
    children?: (sortedItems: PricedItem[]) => React.ReactNode | null;
}

export type SortingBarState = {
    user: User | undefined;
    items: PricedItem[];
    sortBy: ItemField;
    sortDirection: ItemFieldSort;
    searchStr: string;
}

export default class SortingBar extends React.Component<SortingBarProps, SortingBarState> {
    constructor(props: SortingBarProps) {
        super(props);

        this.state = {
            user: undefined,
            items: [],
            sortBy: ItemField.Appeal,
            sortDirection: ItemFieldSort.Descending,
            searchStr: ""
        };

        this.updateItems = this.updateItems.bind(this);
        this.onSearch = this.onSearch.bind(this);
        this.onSort = this.onSort.bind(this);
        this.onSortDirection = this.onSortDirection.bind(this);
        this.getItems = this.getItems.bind(this);
    }

    private async updateItems() {
        const { onLoadingStart, onLoadingStop } = this.props;

        onLoadingStart();
        var items = await this.getItems();
        if (items == null)
            return;

        onLoadingStop();
        this.setState({items});
    }

    private async onSearch(event: React.FormEvent<HTMLFormElement>) {
        event.preventDefault();
        const { searchStr } = this.state;

        const items = await this.getItems({name: searchStr});
        if (items == null)
            return;

        this.setState({items});
    }

    private async onSort(event: React.ChangeEvent<HTMLSelectElement>) {
        const value: ItemField = parseInt(event.target.value) as ItemField;

        const items = await this.getItems({sortBy: value});
        if (items == null)
            return;

        this.setState({sortBy: value, items: items});
    }

    private async onSortDirection() {
        const { sortDirection } = this.state;
        const newDirection: ItemFieldSort = sortDirection === ItemFieldSort.Ascending ? ItemFieldSort.Descending : ItemFieldSort.Ascending;

        const items = await this.getItems({sortDirection: newDirection});
        if (items == null)
            return;

        this.setState({sortDirection: newDirection, items: items});
    }

    private async getItems(options?: ItemQueryOptions): Promise<PricedItem[]> {
        const { user, searchStr, sortBy, sortDirection } = this.state;

        const optionValues: ItemQueryOptions = {
            name: options?.name ?? searchStr,
            sortBy: options?.sortBy ?? sortBy,
            sortDirection: options?.sortDirection ?? sortDirection
        };

        if (!user)
            return [];

        return await ApiService.GetItems(user.access_token, new ItemQuery(optionValues), 1) ?? [];
    }



    render() {
        const { name, children } = this.props;
        const { items, sortBy, sortDirection, searchStr } = this.state;

        const isSortAscending: boolean = sortDirection === ItemFieldSort.Ascending;

        return(
            <>
                <div className="flex flex-row justify-between items-center px-4 py-2 mb-4 bg-gray-900 text-gray-500">
                    <AuthenticationContext.Consumer>
                        {props => {
                            if (props.oidcUser != null && !this.state.user)
                                this.setState({user: props.oidcUser}, () => this.updateItems());

                            return(
                                <></>
                            );
                        }}
                    </AuthenticationContext.Consumer>
                    <span className="">{name}</span>
                    <div className="flex items-center">
                        <form className="flex flex-row mr-4"
                            onSubmit={this.onSearch}>
                            <input type="text" className="pl-2 rounded-l bg-gray-600 text-gray-200"
                                value={searchStr}
                                onChange={event => this.setState({searchStr: event.target.value})} />
                            <button className="flex items-center px-2 rounded-r bg-gray-700 text-gray-300">
                                <FontAwesomeIcon icon="search" className="mr-1 text-xs" />
                                <span className="text-xs">Search</span>
                            </button>
                        </form>
                        <label htmlFor="" className="mr-2 text-sm">Sort By</label>
                        <select name="" id="" className="pl-2 rounded-l h-6 text-sm bg-gray-700 text-gray-300" 
                            value={sortBy} 
                            onChange={this.onSort}>
                            <option value={ItemField.Name} className="">{ItemField[ItemField.Name]}</option>
                            <option value={ItemField.Buys} className="">{ItemField[ItemField.Buys]}</option>
                            <option value={ItemField.Sells} className="">{ItemField[ItemField.Sells]}</option>
                            <option value={ItemField.Appeal} className="">{ItemField[ItemField.Appeal]}</option>
                            <option value={ItemField.Margin} className="">{ItemField[ItemField.Margin]}</option>
                            <option value={ItemField.Sold} className="">{ItemField[ItemField.Sold]}</option>
                            <option value={ItemField.Bought} className="">{ItemField[ItemField.Bought]}</option>
                        </select>
                        <button className="px-2 mr-4 rounded-r border-l border-gray-800 bg-gray-700 text-gray-300"
                            onClick={this.onSortDirection}>
                            {(isSortAscending && sortBy !== ItemField.Name &&
                                <FontAwesomeIcon icon="sort-numeric-up" />
                            ) ||
                            (!isSortAscending && sortBy !== ItemField.Name &&
                                <FontAwesomeIcon icon="sort-numeric-down-alt" />
                            ) ||
                            (isSortAscending && sortBy === ItemField.Name &&
                                <FontAwesomeIcon icon="sort-alpha-up" />
                            ) ||
                            (!isSortAscending && sortBy === ItemField.Name &&
                                <FontAwesomeIcon icon="sort-alpha-down-alt" />
                            )}
                        </button>
                        <button className="mr-4 flex items-center rounded bg-gray-700 text-gray-300" style={{padding: "0.20rem 0.5rem"}}>
                            <FontAwesomeIcon icon="filter" className="mr-1 text-xs" />
                            <span className="text-xs">Filter</span>
                        </button>
                        <button className="py-1 flex items-center rounded bg-gray-700 text-gray-300" style={{padding: "0.20rem 0.5rem"}}
                            onClick={this.updateItems}>
                            <FontAwesomeIcon icon="redo-alt" className="text-sm" />
                        </button>
                    </div>
                </div>
                {children != null ? children(items): null}
            </>
        );
    }
}