import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import React from "react";
import { PricedItem } from "../dataContracts/PricedItem";
import ApiService from "../services/ApiService";
import Price from "./Price";

export type ItemComparisonSlotProps = {
    item: PricedItem;
    deleteItem(itemId: number): Promise<void>;
}

export type ItemComparisonSlotState = {
    amount: number;
}

export default class ItemComparisonSlot extends React.Component<ItemComparisonSlotProps, ItemComparisonSlotState> {
    constructor(props: ItemComparisonSlotProps) {
        super(props);

        this.state = {
            amount: 1
        };

        //this.getNetProfit = this.getNetProfit.bind(this);
    }

    /*
    getNetProfit(): number {
        const { item, amount } = this.state;

        if (item == null)
            return 0;

        const revenue = Math.round(item.SellPrice * amount);
        const expenditure = Math.round(item.BuyPrice * amount);
        const totalFees = item.Fee * amount;
        return revenue - expenditure - totalFees;
    }
    */
    
    render() {
        const { item, deleteItem } = this.props;
        const { amount } = this.state;

        const revenue = Math.round(item.SellPrice * amount);
        const expenditure = Math.round(item.BuyPrice * amount);
        const fee = Math.round(item.SellPrice * 0.15);
        const totalFees = fee * amount;
        const netProfit = revenue - expenditure - totalFees;

        // TODO: Amount is not sorting with items

        return (
            <div className="flex flex-row px-4 py-2 bg-gray-700">
                <div className="flex flex-row w-2/8 items-center mr-6">
                    <img src={item.Icon} alt={item.Name} className="mr-4 w-12 h-12"/>
                    <span className="italic text-gray-200">{item.Name}</span>
                </div>
                <div className="flex flex-col xl:flex-row w-5/12 items-center">
                    <div className="flex flex-row xl:mr-4">
                        <div className="flex flex-col items-center mr-4">
                            <label htmlFor="" className="text-sm mb-1 text-gray-500">Amount</label>
                            <input type="number" className="w-16 px-1 text-sm bg-gray-600 text-gray-200"
                                defaultValue={1}
                                onChange={event => {
                                    const value: number = parseInt(event.target.value);
                                    console.log(value);
                                    if (value != null && !isNaN(value))
                                        this.setState({amount: value});
                                }} />
                        </div>
                        <div className="flex flex-col items-center">
                            <label htmlFor="" className="text-sm mb-1 font-bold text-gray-500">Net Profit</label>
                            <Price price={netProfit} priceColor={`${ netProfit < 0 ? "text-red-500" : "text-green-500" }`} />
                        </div>
                    </div>
                    <div className="flex flex-row">
                        <div className="flex flex-col items-center mr-4">
                            <label htmlFor="" className="text-sm mb-1 text-gray-500">Buys</label>
                            <Price price={expenditure} />
                        </div>
                        <div className="flex flex-col items-center mr-4">
                            <label htmlFor="" className="text-sm mb-1 text-gray-500">Sells</label>
                            <Price price={revenue} />
                        </div>
                        <div className="flex flex-col items-center">
                            <label htmlFor="" className="text-sm mb-1 text-gray-500">Fees</label>
                            <Price price={totalFees} />
                        </div>
                    </div>
                </div>
                <div className="w-2/8">
                    <div className="flex flex-col xl:flex-row justify-evenly w-full h-full">
                        <div className="flex flex-row xl:flex-col items-center xl:mr-4">
                            <label htmlFor="" className="text-sm mr-4 xl:mr-0 xl:mb-1 font-bold text-gray-500">Appeal</label>
                            <span className="text-gray-200">{item.Appeal.toFixed(2)}</span>
                        </div>
                        <div className="flex flex-row xl:flex-col items-center xl:mr-4">
                            <label htmlFor="" className="text-sm mr-4 xl:mr-0 xl:mb-1 text-gray-500">Margin</label>
                            <span className={item.Margin < 15 ? "text-red-500" : "text-green-500"}>{item.Margin.toFixed(2)}%</span>
                        </div>
                        <div className="flex flex-row xl:flex-col items-center xl:mr-4">
                            <label htmlFor="" className="text-sm mr-4 xl:mr-0 xl:mb-1 text-gray-500">Sold</label>
                            <span className="text-gray-200">{item.Sold.toFixed(2)}</span>
                        </div>
                        <div className="flex flex-row xl:flex-col items-center xl:mr-4">
                            <label htmlFor="" className="text-sm mr-4 xl:mr-0 xl:mb-1 text-gray-500">Bought</label>
                            <span className="text-gray-200">{item.Bought.toFixed(2)}</span>
                        </div>
                    </div>
                </div>
                <div className="flex flex-row w-1/12 items-center justify-center">
                    <button className="transition duration-150 ease-in-out px-3 py-1 rounded shadow-lg bg-red-700 text-red-300 hover:bg-red-800 hover:text-red-200"
                        onClick={() => deleteItem(item.Id)}>
                        <FontAwesomeIcon icon="trash-alt" />
                    </button>
                </div>
            </div>
        );

        /*
        return (
            <tr className="bg-gray-700">
                <td className="">
                    <div className="flex flex-row items-center">
                        <img src={item.Icon} alt={item.Name} className="mr-4 w-12 h-12"/>
                        <span className="italic">{item.Name}</span>
                    </div>
                </td>
                <td className="">
                    <div className="flex justify-center">
                        <Price price={item.BuyPrice} />
                    </div>
                </td>
                <td className="">
                    <div className="flex justify-center">
                        <Price price={item.SellPrice} />
                    </div>
                </td>
                <td className="">
                    <div className="flex justify-center">
                        <Price price={item.Fee} />
                    </div>
                </td>
                <td className="">
                    <div className="text-center">
                        {item.AppealValue}
                    </div>
                </td>
                <td className="">
                    <div className="text-center">
                        {item.Margin}
                    </div>
                </td>
                <td className="">
                    <div className="text-center">
                        {item.TransactionRate}
                    </div>
                </td>
            </tr>
        );
        */
    }
}