import Dexie from "dexie";
import { HistoryItem, HistoryItemPrices } from "../dataContracts/HistoryItem";
import { ItemPriceSnapshot } from "../dataContracts/ItemPriceSnapshot";
import { HistoryTimespan } from "../utils/HistoryTimespan";

class LocalDB extends Dexie {
    private historyItemPrices: Dexie.Table<HistoryItemPrices, number>;

    constructor() {
        super("GW2History");

        this.version(1).stores({
            historyItemPrices: "Id"
        });
        this.open().catch(error => {
            console.error(`Opening the db '${this.name}' failed: ${error}`);
        });

        this.historyItemPrices = this.table("historyItemPrices");
        this.historyItemPrices.mapToClass(HistoryItemPrices);
    }

    async getHistoryItemPrices(id: number, timespan: HistoryTimespan): Promise<ItemPriceSnapshot[] | null> {
        const item = await this.historyItemPrices.get(id);
        if (!item)
            return null;
        
        if (timespan === HistoryTimespan.OneWeek)
            return item.History;

        let items: ItemPriceSnapshot[] = [];
        const now = new Date();
        let timeFromNow: number;
        switch (timespan) {
        case HistoryTimespan.OneHour:
            timeFromNow = now.setDate(now.getTime() - (1 * 60 * 60 * 1000));
            break;
        case HistoryTimespan.SixHours:
            timeFromNow = now.setDate(now.getTime() - (6 * 60 * 60 * 1000));
            break;
        case HistoryTimespan.TwelveHours:
            timeFromNow = now.setDate(now.getTime() - (12 * 60 * 60 * 1000));
            break;
        case HistoryTimespan.OneDay:
            timeFromNow = now.setDate(now.getDate() - 1);
            break;
        case HistoryTimespan.ThreeDays:
            timeFromNow = now.setDate(now.getDate() - 3);
            break;
        default:
            timeFromNow = now.setDate(now.getDate() - 7);
            break;
        }
        
        item.History.forEach(price => {
            if (price.time >= timeFromNow)
                items.push(price);
        });

        return items;
    }

    async getNewestTime(id: number): Promise<number> {
        const history = await this.getHistoryItemPrices(id, HistoryTimespan.OneWeek);
        if (!history)
            return 0;
        
        return history[0].time;
    }

    async setHistoryItemPrices(id: number, newHistory: ItemPriceSnapshot[]) {
        const item = await this.historyItemPrices.get(id);
        if (!item) {
            // If no item exists, just create a new item with the given id and history
            const newItem = new HistoryItemPrices(id, newHistory);
            await this.historyItemPrices.add(newItem, newItem.Id);

            return;
        }

        // Else, update the history of the current item to include the new history
        // Unshift (push to front) here because history is sorted by time descending,
        // and the higher the time value, the newer it is.
        item.History.unshift(...newHistory);
        await this.historyItemPrices.update(id, {History: item.History});
    }

    async deleteOldPrices(id: number) {
        const item = await this.historyItemPrices.get(id);
        if (!item)
            return;
        
        const currentPrices: ItemPriceSnapshot[] = [];
        const now: Date = new Date();
        const weekFromNow: number = now.setDate(now.getDate() - 7);
        item.History.forEach(price => {
            if (price.time >= weekFromNow)
                currentPrices.push(price);
        });

        await this.historyItemPrices.update(id, {History: currentPrices});
    }
}

export default new LocalDB();