export class HistoryItemQuery {
    private id: number;
    private newest: number;

    constructor(id: number, newest?: number) {
        this.id = id;
        this.newest = newest ?? 0;
    }

    toObject(): { [key: string]: string } {
        return {
            id: this.id.toString(),
            newest: this.newest.toString()
        }
    }
}